body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'rick_and_mortymedium';
  src: url(/static/media/rick_and_morty_by_shittychan-d9h2vl6-webfont.846e64b3.woff2) format('woff2'),
       url(/static/media/rick_and_morty_by_shittychan-d9h2vl6-webfont.80b90648.woff) format('woff'),
       url(/static/media/rick_and_morty_by_shittychan-d9h2vl6-webfont.5a613064.ttf) format('ttf');
  font-weight: normal;
  font-style: normal;

}

body{
  background: #5e7c7d;
}

.jumbotron{
  background-color: #000;
}

div.jumbotron>div>h1, div.jumbotron>div>p{  
  font-size: 1.7rem;
  font-family: 'rick_and_mortymedium';
  color: white;
  text-align: center;
}

div.jumbotron>div>h1{
font-weight: bold;
letter-spacing: 0.1rem;
  font-size: 4rem;
  color: #15abc6;
  text-shadow: 1px 1px 4px #1AFF00;
}

.nav{
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

section{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

article{
    float: left;
}


.card{
  width: 18rem;
  height: 498px;

  margin-right: 2rem;
  margin-bottom: 2rem;
  border: 3px solid #333;
  background: #eee;
}

.card img{
  width: 240px;
  height: 240px;
  margin: 10px auto;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0px 0px 1px 2px rgba(20,20,20,0.8);
  webkit-box-shadow: 0px 0px 1px 2px rgba(20,20,20,0.8);
}

.list-group {
  display: block;
}

.list-group-item{height: 51px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;}


.card-body.metadata{ 
  font-size: 0.7rem;
  padding: 0;
  margin: 0px 0 0 143px;
  background: #eee;
}

.card-body .icon{
  margin: 0 5px;
}

.card-title{
  margin-bottom: 0.15rem;
  height: 1.5rem;
  white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

}

.card.alive{
  border: 3px solid rgb(50, 197, 160);
}
.card.dead{
  border: 3px solid #ef4242;
}

div>ul.list-group > li.list-group-item:first-child, div>ul.list-group > li.list-group-item:nth-child(2) {
  background: #eee;
}

div.alive>ul.list-group > li.list-group-item:nth-child(3){
  background:rgb(50, 197, 160);
}


div.dead>ul.list-group > li.list-group-item:nth-child(3){
  background:#ef4242;
}

div>ul.list-group > li.list-group-item> .icon{
margin-right: 5px;
}
